import WebApi from "api/WebApi";
export function Login(payload) {
    return WebApi.Post("signin", payload);
}
export function Register(payload) {
    return WebApi.Post("signup", payload);
}
export function Logout() {
    return WebApi.Post("signout", {});
}
export function WhoAmI() {
    return WebApi.Post("user/me", {});
}
export default {
    Login,
    Register,
    Logout
};



