export default class AbstractStore {
    get Match() {
        return this.match || null;
    }
    get Location() {
        return this.location || null;
    }
    get Cookies() {
        return this.cookies || "";
    }
    // protected setCookies(cookies: string) {
    //     this.cookies = cookies;
    // }
    constructor(arg) {
        this.match = arg.match;
        this.location = arg.location;
        this.cookies = arg.cookies;
    }
    // 序列化(转换成plain object, 继承类中继续添加数据)
    toJSON() {
        const { match, location, cookies } = this;
        return {
            match,
            location,
            cookies
        };
    }
    // 反序列化(先创建一个空实例，并使用此方法从json中获取信息填充)
    fromJSON(json) {
        if (!json)
            return this;
        const { match, location, cookies } = json;
        if (typeof match !== "undefined") {
            this.match = match;
        }
        if (typeof location !== "undefined") {
            this.location = location;
        }
        if (typeof cookies !== "undefined") {
            this.cookies = cookies;
        }
        return this;
    }
}



